.gpt3__whatgpt3 {
  display: flex;
  flex-direction: column;
  padding: 4rem 2rem 2rem 2rem;
  background: var(--color-footer);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.gpt3__whatgpt3-feature {
  display: flex;
}
.gpt3__whatgpt3-feature .gpt3__features-container__features {
  margin: 0;
}
.gpt3__whatgpt3-feature .gpt3__features-container__feature {
  flex-direction: column;
}
.gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
  margin-top: 1rem;
  max-width: 700px;
  text-align: justify;
  word-spacing: -1px;
}
.gpt3__whatgpt3-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 4rem 0 2rem; /*for top and bottom only*/
}
.gpt3__whatgpt3-heading h1 {
  font-family: var(--font-family);
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  max-width: 510px;
}
.gpt3__whatgpt3-heading p {
  font-family: var(--font-family);
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  color: var(--color-subtext);
  cursor: pointer;
}
.gpt3__whatgpt3-heading p:hover {
  color: #c98bf3;
}
.gpt3__whatgpt3-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  margin-top: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

.gpt3__whatgpt3-container .gpt3__features-container_feature-text {
  margin-top: 0.5rem;
}

/*for tablet, mobile and smaller devices*/
@media screen and (max-width: 850px) {
  .gpt3__whatgpt3-heading {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0rem;
  }

  .gpt3__whatgpt3-heading p {
    margin-top: 1rem;
  }
}

@media screen and (max-width: 650px) {
  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
    margin-top: 0.5rem;
  }

  .gpt3__whatgpt3-heading h1 {
    font-size: 28px;
    line-height: 40px;
  }
}

@media screen and (max-width: 350px) {
  /* Customizing feature component as per needs */
  .gpt3__whatgpt3-container .gpt3__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}
